@charset "utf-8";


$font-family: "SF Pro SC","SF Pro Text","SF Pro Icons","PingFang SC","Segoe UI",SegoeUI,"Microsoft YaHei","Helvetica Neue","Helvetica","Source Sans Pro",Arial,sans-serif;


$font-family1: "SF Pro SC","SF Pro Text","SF Pro Icons","PingFang SC","Helvetica Neue","Helvetica","Source Sans Pro","Source Han Sans SC","Open Sans","Arial",sans-serif;

$font-family2: "GONGYIXING MC","GONGYIXING M1","SF Pro SC","SF Pro Text","SF Pro Icons","PingFang SC","Helvetica Neue","Helvetica","Source Sans Pro","Open Sans","Arial",sans-serif;

$font-weight-light: 300;
$font-weight-normal: 400;
$font-weight-bold: 700;
$font-weight-black: 900;

// Our variables
$base-font-size:   16px;
$small-font-size:  $base-font-size * 0.875;
$base-line-height: 1.5;

$spacing-unit:     30px;

$text-color:       #333;
$text-size:        17px;
$line-height:      1.7;

$background-color: #fdfdfd;
$brand-color:      #2a7ae2;

$grey-color:       #777;
$grey-color-light: lighten($grey-color, 40%);
$grey-color-dark:  darken($grey-color, 25%);

// Width of the content area
$content-width:    950px;
$md-content-width: 728px;

$on-palm:          620px;
$on-laptop:        800px;
$md-width:         992px;


// Using media queries with like this:
// @include media-query($on-palm) {
//     .wrapper {
//         padding-right: $spacing-unit / 2;
//         padding-left: $spacing-unit / 2;
//     }
// }
@mixin media-query($device) {
    @media screen and (max-width: $device) {
        @content;
    }
}

@mixin font_bold(){
  font-weight: 700;
  font-style: normal;
}

@mixin font_light(){
  font-weight: 300;
  font-style: normal;
}

@mixin font_light_italic(){
  font-weight: 300;
  font-style: italic;
}

@mixin font_normal(){
  font-weight: 400;
  font-style: normal;
}

@mixin font-normal_italic(){
  font-weight: 400;
  font-style: normal;
}

@mixin font_black(){
  font-weight: 900;
  font-style: normal;
}



// Import partials from `sass_dir` (defaults to `_sass`)
@import
        "copyrights",
        "reset",
        "gongyixing_fonts",
        "normalize",
        "bootstrap",
        "base",
        "layout",
        "brand-lite"
        ,"site_analytics"
;
