
.container.center-padding {
    padding-right: 120px;
    padding-left: 120px;
}
.container {
    margin-right: auto;
    margin-left: auto;
}
.w-container {
    margin-left: auto;
    margin-right: auto;
    max-width: 940px;
}

.h1 {
    margin-top: 0;
    margin-bottom: 40px;
    font-size: 4em;
    letter-spacing: -2px;
}
.h1, .h1.white {
    line-height: 44px;
    font-weight: 600;
}

.paragraph {
    overflow: hidden;
    color: #525252;
    font-size: 1.2em;
    line-height: 1.4em;
    letter-spacing: .01em;
}

.center {
    display: flex;
    -webkit-flex-direction: column;
    -ms-flex-direction: column;
    flex-direction: column;
    -webkit-box-align: center;
    -webkit-align-items: center;
    -ms-flex-align: center;
    align-items: center;
    text-align: center;
}


.button.v1 {
    margin-top: 30px;
    margin-bottom: 90px;
}

.button {
    display: inline-block;
    padding: 14px 50px;
    border-radius: 5px;
    background-color: #f5016c;
    -webkit-transition: background-color .2s ease,box-shadow .2s ease,-webkit-transform .2s ease;
    transition: background-color .2s ease,box-shadow .2s ease,-webkit-transform .2s ease;
    transition: background-color .2s ease,transform .2s ease,box-shadow .2s ease;
    transition: background-color .2s ease,transform .2s ease,box-shadow .2s ease,-webkit-transform .2s ease;
    font-size: 1.5em;
    line-height: 1.3em;
    font-weight: 600;
    letter-spacing: .02em;
}

.w-button {
    display: inline-block;
    padding: 9px 15px;
    background-color: #3898EC;
    color: #fff;
    border: 0;
    line-height: inherit;
    text-decoration: none;
    cursor: pointer;
    border-radius: 0;
}

