.cookie-pop{
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background: rgba(0,0,0,0.5);
  z-index: 999999999999;
  display: none;
}
.cookie-pop .pop{
  width: 782px;
  box-sizing: border-box;
  padding: 0 72px;
  background: #fff;
  left: 50%;
  top: 50%;
  position: absolute;
  transform: translate(-50%,-50%);
  -webkit-transform: translate(-50%,-50%);
  -moz-transform: translate(-50%,-50%);
  -ms-transform: translate(-50%,-50%);
  -o-transform: translate(-50%,-50%);
  padding-bottom: 64px;
  display: none;
}
.cookie-pop .pop.active{
  display: block;
}
.cookie-pop .pop .close-btn{
  position: absolute;
  right: 32px;
  top: 32px;
  width: 24px;
  height: 24px;
  background: url(../img/index/pop-close.png) no-repeat center;
  background-size: 24px 24px;
  cursor: pointer;
}
.cookie-pop .pop .title{
  font-size: 30px;
  color: #2A2E2E;
  letter-spacing: 0;
  width: 100%;
  text-align: left;
  margin-top: 64px;
  padding-bottom: 24px;
  border-bottom: 1px solid #E9ECF0;
}
.cookie-pop .pop .content{
  font-size: 14px;
  color: #5F6464;
  letter-spacing: 0;
  line-height: 25px;
  padding-top: 24px;
  margin-bottom: 82px;
}
.cookie-pop .pop .content .label{
  margin-bottom: 30px;
}
.cookie-pop .pop .content a{
  color: #5F6464;
  border-bottom: 1px solid #333;
}
.cookie-pop .btn-box{
  width: 100%;
  overflow: hidden;
}
.cookie-pop .btn-box .btn{
  float: right;
  width: 200px;
  min-height: 40px;
  height: auto;
  margin-right: 16px;

  text-align: center;
  font-size: 14px;
  color: #2A2E2E;
  background: #F2F3F5;
  cursor: pointer;
}
.cookie-pop .btn-box .btn:hover{
  background: #9fa1a3;
  color: #fff;
}

@media screen and (max-width: 820px) {

}

.cookie-pop .pop{
    margin: 0 12px;
    width: auto;
    padding: 0 28px;
    left: auto;
    top: 50%;
    -webkit-transform: translate(0,-50%);
    padding-bottom: 37px;
  }
  .cookie-pop .pop .title{
    margin-top: 35px;
    padding-right: 16%;
    box-sizing: border-box;
  }
  .cookie-pop .pop .close-btn{
    width: 30px;
    height: 30px;
    background-size: 30px 30px;
    right: 28px;
    top: 28px;
  }
  .cookie-pop .pop .title{
    font-size: 24px;
    line-height: 36px;
    padding-bottom: 16px;
  }
  .cookie-pop .pop .content{
    padding-top: 12px;
    margin-bottom: 26px;
  }
  .cookie-pop .pop .btn-box{
    width: 100%;
  }
  .cookie-pop .pop .btn-box .btn{
    width: 100%;
    float: left;
    margin-bottom: 12px;
  }
  .cookie-pop .pop .btn-box .btn:last-child {
    margin-bottom: 0;
  }

.cookie-pop {
  text-align: center;
}
.cookie-pop .pop.active{
  left: 20%;
  width: 60%;
  /*width: 782px;*/
  max-width: 798px;
  min-width: 260px;
  text-align: left;
}

.cookie-pop .btn-box {
  text-align: left;
}

.cookie-pop .pop .btn-box .btn {
     width: 200px; 
    float: right;
    margin-bottom: 12px;
}


.cookie-pop .btn-box .accept-btn:hover{
  background: rgba(141, 209, 110, 1);
  color: #fff;
}

.cookie-pop .pop .title{
    font-size: 35px;
}
.cookie-pop .pop .content .label {
    margin-top: 30px;
}

.cookie-pop .pop .content {
  font-size: 1.2em;
  margin-bottom: 3em;
}

  .cookie-pop .pop .btn-box .btn {
