  }

@media screen and (max-width: 820px) {
  .cookie-pop .pop.active{
    left: 10%;
    width: 80%;
    text-align: left;
  }
}

@media screen and (max-width: 420px) {
  .cookie-pop .pop.active{
    left: 1%;
    width: 98%;
    text-align: center;
  }
  .cookie-pop .pop .title {
    padding-right: 0;
  }
  .cookie-pop .pop .label, .cookie-pop .pop .desc {
    text-align: left;
  }

  .cookie-pop .pop {
    margin: 0;
  }

  .cookie-pop .pop .btn-box .btn {
      float: right;
      margin-bottom: 12px;
  }


}